<template>
  <div v-bind:class="userContainerMode">
    <div class="profile_picture_container">
      <img
        class="mobile_img_class"
        v-bind:src="githubResponse.avatar"
        alt="Profile Picture"
      />
    </div>
    <div class="user_info_container">
      <div class="name_user_container">
        <div class="user_title_container">
          <h1>{{ nameChecker }}</h1>
          <p>Joined {{ dateConverter }}</p>
        </div>
        <div class="username_container">
          <a v-bind:href="githubResponse.profileUrl" target="_blank"
            >@{{ githubResponse.login }}</a
          >
        </div>
      </div>
      <div class="responsive_container">
        <div class="bio_container">
          <p class="truncate_text" v-bind:class="{ greyedOut: greyedOutBio }">{{ bioChecker }}</p>
        </div>
        <div v-bind:class="metadataContainerMode">
          <div>
            <h4>Repos</h4>
            <h2>{{ githubResponse.repos }}</h2>
          </div>
          <div>
            <h4>Followers</h4>
            <h2>{{ githubResponse.followers }}</h2>
          </div>
          <div>
            <h4>Following</h4>
            <h2>{{ githubResponse.following }}</h2>
          </div>
        </div>
        <div class="metadata_2_container" id="metadata_2_container_id">
          <div class="metadata_2_subcontainer">
            <div
              class="metadata_2_items"
              v-bind:class="{ greyedOut: greyedOutLocation }"
            >
              <img
                v-bind:class="svgMode"
                src="@/assets/icon-location.svg"
                alt=""
              />
              <a
                target="_blank"
                v-if="locationChecker !== 'Not Available'"
                v-bind:href="`https://www.google.com/maps/place/${locationChecker}`"
                ><span>{{ locationChecker }}</span></a
              >
              <span v-else>{{ locationChecker }}</span>
            </div>
            <div
              class="metadata_2_items"
              v-bind:class="{ greyedOut: greyedOutBlog }"
            >
              <img
                v-bind:class="svgMode"
                src="@/assets/icon-website.svg"
                alt=""
              />
              <a
                target="_blank"
                v-if="blogChecker !== 'Not Available'"
                v-bind:href="blogChecker"
                ><span>{{ blogChecker }}</span></a
              >
              <span v-else>{{ blogChecker }}</span>
            </div>
          </div>
          <div class="metadata_2_subcontainer">
            <div
              class="metadata_2_items"
              v-bind:class="{ greyedOut: greyedOutTwitter }"
            >
              <img
                v-bind:class="svgMode"
                src="@/assets/icon-twitter.svg"
                alt=""
              />
              <a
                target="_blank"
                v-if="twitterChecker !== 'Not Available'"
                v-bind:href="`https://twitter.com/${twitterChecker}`"
                ><span>{{ twitterChecker }}</span></a
              >
              <span v-else>{{ twitterChecker }}</span>
            </div>
            <div
              class="metadata_2_items"
              v-bind:class="{ greyedOut: greyedOutCompany }"
            >
              <img
                v-bind:class="svgMode"
                src="@/assets/icon-company.svg"
                alt=""
              />
              <a
                target="_block"
                v-if="companyChecker !== 'Not Available'"
                v-bind:href="`https://www.google.com/search?q=${companyChecker}`"
                ><span>{{ companyChecker }}</span></a
              >
              <span v-else>{{ companyChecker }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// v-bind:class="[{ greyedOut: greyedOutLocation }, metadata_2_items]"
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["githubResponse", "mode"]),

    nameChecker() {
      if (!this.githubResponse.name) {
        let arr = this.githubResponse.login.split(" ");
        for (var i = 0; i < arr.length; i++) {
          arr[i] = arr[i].charAt(0).toUpperCase() + arr[i].slice(1);
        }

        let str2 = arr.join(" ");
        return str2;
      } else {
        return this.githubResponse.name;
      }
    },

    bioChecker() {
      if (!this.githubResponse.profileBio) {
        return "This profile has no bio";
      } else {
        return this.githubResponse.profileBio;
      }
    },

    userContainerMode() {
      if (this.mode) {
        return "user_container_dark";
      } else {
        return "user_container_light";
      }
    },

    svgMode() {
      if (this.mode) {
        return "svg_size_dark";
      } else {
        return "svg_size";
      }
    },

    metadataContainerMode() {
      if (this.mode) {
        return "metadata_container_dark";
      } else {
        return "metadata_container_light";
      }
    },

    companyChecker() {
      if (!this.githubResponse.company) {
        return "Not Available";
      } else {
        return this.githubResponse.company;
      }
    },

    locationChecker() {
      if (!this.githubResponse.location) {
        return "Not Available";
      } else {
        return this.githubResponse.location;
      }
    },

    blogChecker() {
      if (!this.githubResponse.githubBlog) {
        return "Not Available";
      } else {
        return this.githubResponse.githubBlog;
      }
    },

    twitterChecker() {
      if (!this.githubResponse.twitter) {
        return "Not Available";
      } else {
        return this.githubResponse.twitter;
      }
    },

    dateConverter() {
      // split  based on whitespace, then get except the first element
      // and then join again
      return new Date(this.githubResponse.joinedAt)
        .toDateString()
        .split(" ")
        .slice(1)
        .join(" ");
    },

    greyedOutTwitter() {
      return this.twitterChecker == "Not Available";
    },

    greyedOutLocation() {
      return this.locationChecker == "Not Available";
    },

    greyedOutCompany() {
      return this.companyChecker == "Not Available";
    },

    greyedOutBio() {
      return this.bioChecker == "This profile has no bio";
    },

    greyedOutBlog() {
      return this.blogChecker == "Not Available";
    },
  },
};
</script>

<style scoped>
@media (min-width: 992px) {
  img {
    width: 117px;
  }

  .truncate_text {
    width: 500px;
  }

  .user_container_dark,
  .user_container_light {
    padding: 35px 25px 15px 25px;
    display: flex;
  }

  .user_info_container {
    padding: 0px 25px;
  }

  .user_title_container {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  #metadata_2_container_id {
    padding: 25px 0px 0px 0px;
  }
}

@media (min-width: 320px) and (max-width: 767px) {
  .mobile_img_class {
    width: 70px;
    position: absolute;
    top: 10px;
  }

   .truncate_text {
    width: 300px;
  }

  .username_container {
    position: relative;
    left: 92px;
    width: 200px;
  }

  .user_title_container {
    flex-direction: column;
    position: relative;
    left: 90px;
    width: 250px;
  }

  .metadata_2_container {
    flex-direction: column;
  }

  .profile_picture_container {
    position: relative;
  }

  .user_container_dark,
  .user_container_light {
    padding: 25px 25px 0px 25px;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  /* CSS Rules */
  .user_title_container,
  .user_container_dark,
  .user_container_light {
    position: relative;
  }

  .truncate_text {
    width: 500px;
  }

  .user_title_container {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .name_user_container {
    position: relative;
    left: 150px;
    top: 9px;
    width: 250px;
  }

  .responsive_container {
    margin-top: 50px;
  }

  .profile_picture_container img {
    width: 117px;
  }

  .user_container_dark,
  .user_container_light {
    padding: 40px 25px 0px 25px;
  }

  /*.name_user_container{
    position: absolute;
    bottom: 452px;
    right: 200px;
    width: 200px;
  }*/

  .profile_picture_container {
    position: absolute;
  }

  .user_title_container p {
    position: absolute;
    top: 65px;
  }
}

.truncate_text{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.svg_size,
.svg_size_dark {
  max-width: 15px;
  margin-right: 15px;
  border-radius: 0px;
}

.svg_size_dark {
  filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg)
    brightness(102%) contrast(102%);
}

a:hover {
  text-decoration: underline;
}

.greyedOut {
  filter: invert(55%) sepia(1%) saturate(0%) hue-rotate(183deg) brightness(150%)
    contrast(85%);
}
.user_info_dark {
  background-color: #1e2a47;
}
p,
h1 {
  margin: 0px;
}

h2,
h4 {
  margin: 5px;
}

h4 {
  color: #4b6a9b;
}

.user_title_container {
  display: flex;
}

.metadata_container_dark,
.metadata_container_light {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 30px;
  border-radius: 10px;
}

.metadata_2_items {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 0px;
}

.bio_container,
.metadata_2_container {
  padding: 35px 0px;
}

img {
  border-radius: 100%;
}
.user_container_light,
.user_container_dark {
  border-radius: 15px;
}

.user_container_light{
  box-shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.198567);
}

.user_container_light {
  background-color: #fefefe;
}

.metadata_container_light {
  background-color: #f6f8ff;
}

.user_container_dark {
  background-color: #1e2a47;
  color: white;
}

.user_container_dark h4,
.user_container_dark p,
.user_container_dark .metadata_2_container a,
.user_container_dark .metadata_2_container span {
  color: white;
}

.user_container_dark .metadata_2_container .svg-size {
  filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg)
    brightness(102%) contrast(102%);
}

.metadata_container_dark {
  background-color: #141d2f;
}

.user_info_container {
  width: 100%;
}

.metadata_2_container {
  display: flex;
  justify-content: space-between;
}

a {
  text-decoration: none;
  color: #0079ff;
}
</style>
