<template>
  <div v-bind:class="appModeClass">
    <dev-finder></dev-finder>
  </div>
</template>

<script>
import DevFinder from "./components/DevFinder.vue";
import { mapGetters } from "vuex";
export default {
  components: {
    DevFinder,
  },

  computed: {
    ...mapGetters(["mode"]),

    appModeClass() {
      if (!this.mode) {
        return "main_app_light";
      } else {
        return "main_app_dark";
      }
    },
  },
};
</script>>

