<template>
  <div v-bind:class="{mode_container_dark: mode}" class="mode_container">
    <h1>devfinder</h1>
    <div class="button_container">
      <button v-bind:class="{img_dark_mode: mode}" v-on:click="changeMode">
        {{ modeChecker }}
        <img v-if="!mode" src="@/assets/icon-moon.svg" alt="dark mode" />
        <img v-else src="@/assets/icon-sun.svg" alt="light mode" />
      </button>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { mapGetters } from "vuex";
export default {
  methods: {
    ...mapMutations(["changeMode"]),
  },
  computed: {
    ...mapGetters(["mode"]),

    modeChecker() {
      if (!this.mode) {
        return "DARK";
      } else {
        return "LIGHT";
      }
    },
  },
};
</script>

<style scoped>
img {
  filter: invert(46%) sepia(45%) saturate(272%) hue-rotate(177deg)
    brightness(95%) contrast(86%);
}

.img_dark_mode{
  filter: invert(100%) sepia(98%) saturate(0%) hue-rotate(304deg) brightness(101%) contrast(10000%);
}

.img_dark_mode:hover{
  filter: invert(70%) sepia(39%) saturate(330%) hue-rotate(185deg) brightness(97%) contrast(90%);
}

button {
  display: flex;
  width: 80px;
  justify-content: space-between;
  color: #697c9a;
  background-color: inherit;
  border: none;
}

button:hover {
  color: black;
  filter: invert(0%) sepia(0%) saturate(0%) hue-rotate(33deg) brightness(0%) contrast(103%);
}

h1 {
  margin: 0px;
}

.button_container {
  display: flex;
  align-items: center;
}

.mode_container {
  display: flex;
  justify-content: space-between;
}

.mode_container_dark{
  color: white;
}
</style>