import { createStore } from 'vuex'

let store = createStore({
    state() {
        return {
            githubReturn: {
                login: "",
                avatar: "",
                name: "Edwin",
                joinedAt: "",
                profileBio: "",
                repos: "",
                followers: "",
                following: "",
                location: "",
                twitter: "",
                githubBlog: "",
                company: "",
                profileUrl: "",
            },
            darkMode: false,
            userIsFalse: false
        }
    },

    actions: {
        githubSearch(context, payload) {
            console.log(context);
            console.log(payload)
            context.commit('changeStatus', false)
            fetch(`https://api.github.com/users/${payload.userName}`)
                .then(response => {
                    if (!response.ok) {
                        context.commit('changeStatus', true)
                        console.log(response);
                        throw new Error(`Network response returned ${response.status}`);
                    } else {
                        return response.json()
                    }
                })
                .then(data => {
                    console.log(data);
                    context.commit('githubUser', data)
                })
                .catch(error => {
                    console.error(`Problem with fetch operation. ${error}`)
                })
        }
    },

    getters: {
        githubResponse(state) {
            return state.githubReturn
        },

        status(state) {
            return state.userIsFalse;
        },

        mode(state){
           return state.darkMode;
        }
    },

    mutations: {
        changeStatus(state, booleanValue) {
            state.userIsFalse = booleanValue;
        },

        changeMode(state){
            state.darkMode = !state.darkMode
        },

        githubUser(state, githubData) {
            state.githubReturn.login = githubData.login;
            state.githubReturn.avatar = githubData.avatar_url;
            state.githubReturn.name = githubData.name;
            state.githubReturn.joinedAt = githubData.created_at;
            state.githubReturn.profileBio = githubData.bio;
            state.githubReturn.repos = githubData.public_repos;
            state.githubReturn.followers = githubData.followers;
            state.githubReturn.following = githubData.following;
            state.githubReturn.location = githubData.location;
            state.githubReturn.twitter = githubData.twitter_username;
            state.githubReturn.githubBlog = githubData.blog;
            state.githubReturn.company = githubData.company;
            state.githubReturn.profileUrl = githubData.html_url
            console.log(state.githubReturn);
        }
    }


});

export default store