<template>
  <div>
    <div v-bind:class="searchModeClass">
      <div v-bind:class="searchSubContMode">
        <img src="@/assets/icon-search.svg" alt="" />
        <input
          v-bind:class="inputDarkMode"
          v-model="username"
          placeholder="Search GitHub username..."
          type="text"
        />
      </div>
      <div class="button_container">
        <p v-if="status">No results</p>
        <button v-on:click="userSearch(username)">Search</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      username: "",
    };
  },

  computed: {
    ...mapGetters(["status", "mode"]),

    inputDarkMode() {
      if (this.mode) {
        return "input_dark";
      } else {
        return 0;
      }
    },

    searchModeClass() {
      if (this.mode) {
        return "search_container_dark";
      } else {
        return "search_container_light";
      }
    },

    searchSubContMode() {
      if (this.mode) {
        return "search_sub_container_dark";
      } else {
        return "search_sub_container_light";
      }
    },
  },

  created() {
    this.userSearch("octocat");
  },

  methods: {
    userSearch(user) {
      this.$store.dispatch("githubSearch", {
        userName: user,
      });
      this.username = "";
    },
  },
};
</script>

<style scoped>
@media (min-width: 320px) and (max-width: 767px)  {
  input {
    width: 184px;
  }
}

@media (min-width: 992px){
  input{
    width: 300px
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  input {
    width: 300px;
  }
}
p {
  margin: 0px 15px;
  color: red;
  font-weight: bold;
}
.search_container_light,
.search_container_dark,
.search_sub_container_light,
.search_sub_container_dark,
.button_container {
  display: flex;
  align-items: center;
}

.search_container_light,
.search_container_dark {
  padding: 15px 25px;
  margin: 25px 0px;
  justify-content: space-between;
  border-radius: 15px;
}

.search_container_light{
  box-shadow: 0px 16px 30px -10px rgba(70, 96, 187, 0.198567);
}

.search_container_dark,
.search_sub_container_dark {
  background-color: #1e2a47;
}

.search_container_light {
  background-color: #fefefe;
}

input {
  border: none;
  caret-color: #0079ff;
  background-color: inherit;
}

.input_dark {
  color: white;
}

.input_dark::placeholder {
  color: rgb(175, 175, 175);
}

input:focus {
  outline: none;
}

button {
  /*background-color: #60abff;*/
  background-color: #0079ff;
  color: #fefefe;
  border: none;
  padding: 10px 25px;
  border-radius: 10px;
}

button:hover{
  background-color: #60abff;
}

img {
  margin-right: 20px;
}
</style>